const express = require('express');
const authMiddleware = require('../middleware/authMiddleware');
const ContractController = require('../controllers/ContractController');
const asyncHandler = require('express-async-handler');

const router = express.Router();

// @desc    Listar todos os contratos
// @route   GET /api/contracts
// @access  Private
router.get('/', authMiddleware, asyncHandler(ContractController.getAllContracts));

// @desc    Obter contrato por ID
// @route   GET /api/contracts/:id
// @access  Private
router.get('/:id', authMiddleware, asyncHandler(ContractController.getContractById));

// @desc    Criar novo contrato
// @route   POST /api/contracts
// @access  Private
router.post('/', authMiddleware, asyncHandler(ContractController.createContract));

// @desc    Atualizar contrato
// @route   PUT /api/contracts/:id
// @access  Private
router.put('/:id', authMiddleware, asyncHandler(ContractController.updateContract));

// @desc    Alterar status do contrato
// @route   PATCH /api/contracts/:id/status
// @access  Private
router.patch('/:id/status', authMiddleware, asyncHandler(ContractController.updateContractStatus));

// @desc    Deletar contrato
// @route   DELETE /api/contracts/:id
// @access  Private (Admin only)
router.delete('/:id', authMiddleware, asyncHandler(ContractController.deleteContract));

// @desc    Gerar parcelas do contrato
// @route   POST /api/contracts/:id/generate-installments
// @access  Private
router.post('/:id/generate-installments', authMiddleware, asyncHandler(ContractController.generateInstallments));

module.exports = router;